<template>
  <div>
    <HeaderTitle @arrowLeft="()=>$router.back()"></HeaderTitle>
    <div class="cinemaDetilsName">
      <h3>{{cinemaDetilsMsg.name}}</h3>
    </div>
    <div class="detilsMenu">
      <span class="iconfont icondiqiu"></span>
      <p>{{cinemaDetilsMsg.address}}</p>
      <i class="iconfont icondianhua"></i>
    </div>

    <div class="cinemaDetilsFilms">
      <div
        class="ImgBackground"
        :style="{background: `url(${filmPoster}) no-repeat`, backgroundSize: '100% 100%',}"
      ></div>

      <div class="detils-swiper">
        <swiperComponents :name="'detils-loop'">
          <div
            @click="clickDetilsLoop(film)"
            v-for="(film,index) in filmDetilsMsg"
            :key="film.filmId"
            class="swiper-slide detilsPic"
            v-swiper="{index:index,datalist:filmDetilsMsg,num:'auto',space:30,centeredSlides:true,name:'detils-loop'}"
          >
            <img :src="film.poster" alt />
          </div>
        </swiperComponents>
      </div>
    </div>

    <div class="cinemaDetilsMsg">
      <div class="cinemaDetilsMsgTitle">
        <h4>{{filmMsg.name}}</h4>
        <p>{{filmMsg.grade}}</p>
      </div>
      <div class="cinemaDetilsMsgtxt">
        <h5>{{filmMsg.category}} |</h5>
        <span>{{filmMsg.runtime}}分钟 |</span>
        <i>{{filmMsg.actors | ActorFilter}}</i>
      </div>
    </div>
    <div class="cinemaMenuClass">
      <div
        v-for="(filmD,index) in filmDate"
        :key="filmD"
        :class="['cinemaDetilsBottmMenu',index===defaultDate?'active':'']"
        @click="MenuClick(index)"
      >{{filmD | filterTimeMMDD}}</div>
    </div>
  </div>
</template>

<script>
import { instance } from "@/utils/http";
export default {
  data() {
    return {
      defaultDate: 0,
      activeClass: false,
      filmMsg: "",
      filmDate: [],
      filmPoster: "",
      cinemaDetilsMsg: "",
      filmDetilsMsg: ""
    };
  },
  methods: {
    MenuClick(index) {
      this.defaultDate = index;
    },
    clickDetilsLoop(film) {
      this.filmPoster = film.poster;
      this.filmDate = film.showDate;
      instance(`/gateway?filmId=${film.filmId}&k=3355024`, {
        headers: {
          "X-Host": "mall.film-ticket.film.info"
        }
      }).then(res => {
        this.filmMsg = res.data.data.film;
      });
    }
  },
  components: {
    HeaderTitle: () => import("@/components/headerTitle"),
    swiperComponents: () => import("@/components/swiperComponents")
  },
  created() {
    instance(`/gateway/?cinemaId=${this.$route.params.CinemaId}&k=6873185`, {
      headers: {
        "X-Host": "mall.film-ticket.cinema.info"
      }
    }).then(res => {
      this.cinemaDetilsMsg = res.data.data.cinema;
    });
    instance(`/gateway/?cinemaId=${this.$route.params.CinemaId}&k=7306602`, {
      headers: {
        "X-Host": "mall.film-ticket.film.cinema-show-film"
      }
    }).then(res => {
      this.filmDetilsMsg = res.data.data.films;
      console.log(this.filmDetilsMsg);
      if (this.filmDetilsMsg[0]) {
        this.clickDetilsLoop(this.filmDetilsMsg[0]);
      }
    });
  }
};
</script>
<style lang="scss">
.cinemaMenuClass {
  padding-left: 15px;
  width: 100%;
  height: 8vh;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-bottom: 1px solid #ededed;
  box-sizing: border-box;
  position: relative;
  overflow-x: auto;
  $scrollBarSize: 10px;
      &::-webkit-scrollbar {
        width: $scrollBarSize;
        height: $scrollBarSize;
        background-color: transparent;
        display: none;
      }

      & .-o-scrollbar {
        display: none;
      }
  .cinemaDetilsBottmMenu {
    line-height: 8vh;
    height: 100%;
    padding: 0 15px;
    flex-shrink: 0;
    box-sizing: border-box;
  }
  .active {
    box-sizing: border-box;
    color: #ff5f16;
    border-bottom: 1px solid #ff5f16;
  }
}
.detils-swiper {
  position: absolute;
  top: 10%;
  width: 100%;
  height: 100%;
  overflow: hidden;
  .detilsPic {
    width: 90px;
    height: 130px;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
.cinemaDetilsFilms {
  width: 100%;
  height: 160px;
  position: relative;
  overflow: hidden;
  .ImgBackground {
    width: 100%;
    height: 100%;
    padding: 15px 0;
    filter: blur(30px);
  }
}
.cinemaDetilsName {
  margin-top: 44px;
  height: 44px;
  text-align: center;
  font-size: 17px;
  color: #191a1b;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
  padding: 0 15px;
  background: #fff;
}
.detilsMenu {
  display: flex;
  justify-content: space-between;
  padding: 0 17px;
  height: 50px;
  align-items: center;
}
</style>